.methodology {
    &__inner {
        padding: 0 33px 60px;
        max-width: 414px;
        margin: 0 auto;
    }
    &__header {
        margin-bottom: 40px;
        h1 {
            font-family: 'PF DinDisplay Pro';
            font-size: 30px;
            font-weight: 900;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            letter-spacing: 1.88px;
            color: #002337;
            margin-bottom: 10px;
            text-transform: uppercase;
        }
        p {
            font-family: 'PF DinDisplay Pro';
            font-size: 14px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.36;
            letter-spacing: normal;
            color: #7b919e;
        }
    }
    &__body {}
    &__info {
        max-width: 414px;
        color: #002337;
        h2 {
            font-family: 'PF DinDisplay Pro';
            font-size: 24px;
            font-weight: 900;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.17;
            letter-spacing: 1.3px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
        h3 {
            font-family: 'PF DinDisplay Pro';
            font-size: 22px;
            font-weight: 700;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.17;
            letter-spacing: 1.3px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
        h4 {
            font-family: 'PF DinDisplay Pro';
            font-size: 20px;
            font-weight: 700;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.17;
            letter-spacing: 1.3px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
        h5 {
            font-family: 'PF DinDisplay Pro';
            font-size: 18px;
            font-weight: 700;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.17;
            letter-spacing: 1.3px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
        h6 {
            font-family: 'PF DinDisplay Pro';
            font-size: 16px;
            font-weight: 500;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.17;
            letter-spacing: 1.3px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
        p {}
        ul {
            padding-left: 16px;
            li {
                list-style: disc;
                margin-bottom: 10px;
                padding-left: 10px;
            }
        }
        ol {
            padding-left: 16px;
            li {
                list-style: decimal;
                margin-bottom: 10px;
                padding-left: 10px;
            }
        }
        img {
            padding: 0;
            width: 100%;
            height: auto;
            margin-bottom: 40px;
        }
        blockquote {
            max-width: 414px;
            background-color: #c5f0bd;
            padding: 40px 33px;
            margin: 0 -33px 16px;
            p {
                padding-left: 0;
            }
        }
        hr {
            appearance: none;
            color: #e61841;
            height: 2px;
            background-color: #e61841;
            border:none;
            margin: 20px 0;
        }
    }
    &__video {
         width: 100%;
         height: 190px;
         margin: 0 auto 47px;
    }
    &__files {
        margin-bottom: 45px;
    }
    &__file {
         display: inline-block;
         position: relative;
         padding-top: 67px;
         margin-right: 20px;
         font-family: "PF DinDisplay Pro";
         font-size: 14px;
         font-weight: 300;
         font-stretch: normal;
         font-style: normal;
         line-height: 1.36;
         letter-spacing: normal;
         text-align: right;
         color: #e61841;
        &:before {
            position: absolute;
            content: '';
            width: 42px;
            height: 57px;
            top: 0;
            box-shadow: 0 2px 5px 0 rgba(82, 65, 30, 0.19), 0 5px 18px 0 rgba(174, 148, 113, 0.22);
            background: url("/images/desktop/icons/PDF@2x.png") 50% 50%/contain no-repeat;
        }
    }
}