.contacts {
    &-header {
        #map {
            position: relative;
            width: 100%;
            height: 316px;
            margin-bottom: 31px;
            &:after {
                 position: absolute;
                 content: '';
                 background-image: url("images/desktop/icons/clouds-small.svg");
                 width: 100%;
                 height: 95px;
                 background-repeat: repeat-x;
                 bottom: -20px;
             }
        }
    }
    &-block {
        margin-bottom: 40px;
        &__inner {
             padding: 0 33px;
             max-width: 414px;
             margin: 0 auto;
        }
        &__title {}
        &__row {
            display: table-row;
            font-size: 14px;
        }
        &__cell {
            display: table-cell;
            p {
                margin-bottom: 0;
                padding-right: 10px;
            }
        }
        &__contacts {}
        &__phones {
            &-number {
                padding: 0 35px 15px 0;
                white-space: nowrap;
            }
            &-button {
                 vertical-align: middle;
                a {
                    padding: 12px 15px;
                    text-decoration: none;
                    font-size: 10px;
                    letter-spacing: 0.28px;
                }
            }
        }
        &__email {}
        &__text {
            b {
                font-weight: 500;
            }
            a {
                display: block;
                &.text-link {
                    display: inline;
                }
            }
        }
        &__images {
            display: flex;
            flex-wrap: wrap;
            div {
                width: 33.3%;
                /*flex-grow: 1;*/
            }
            img {
                width: 100%;
            }
        }
    }
}